#app2 {
    border: 1px solid blue;
}

#app2 .tab-bar{
    display: flex;
}
#app2 .tab-bar > li{
    border: 3px solid #000;
    width: 50%;
}
#app2 .tab-bar > li.selected{
    background: rgba(253, 221, 155, 1);
}

#app2 .tab-content > li{
    display: none;
}

#app2 .tab-content > li.ac{
    display: block;
}
